import React, { useState,Component } from "react";
import NavBar from "./NavBar";
import "../Styles/Create.css";
import axios from "axios";
import { connect } from "react-redux";
import TopNavBar from "./TopNavBar";
import {
  getBackground,
  getHead,
  getOutfit,
} from "../Redux/Reducers/pngReducer";

import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import mergeImages from "merge-images";
import { FaList } from "react-icons/fa";

class Create extends Component {
  constructor(props) {
    super(props);
    this.state = {
      layer: [
        { name: "background", id: "0" },
        { name: "head", id: "1" },
        { name: "outfit", id: "2" },
      ],
      background: "",
      head: "",
      outfit: "",
      png: "",
      mint: "1000",
    };
  }
  componentDidMount() {
    this.props.getBackground();
    this.props.getHead();
    this.props.getOutfit();

    console.log(this.state.background.back);
  }
  merge = () => {
    mergeImages(this.state.background).then(
      (b64) => (document.querySelector("img").src = b64)
    );
  };
  getDiv = (png) => {
    this.setState({ background: png });
    console.log(this.state.background.back);
  };

  showFile = (e) => {
    // const text = (file[0].target.value)
    console.log(e.target.files[0].name);
    this.setState({ outfit: e.target.files[0].name });
    // alert(text)
  };

  getHead = (png) => {
    this.setState({ head: png });
    console.log(png);
  };
  getOutfit = (png) => {
    this.setState({ outfit: png });
    console.log(png);
  };

handleChange = (e) => {
  this.setState({ mint: e.target.value });
};

  checkUploadResult = (error, resultEvent) => {
    if (resultEvent.event === "success") {
      console.log("Picture uploaded successfully");
      console.log(resultEvent.info.url);
      this.setState({ background: resultEvent.info.url });
    }
  };
  c = (error, resultEvent) => {
    if (resultEvent.event === "success") {
      console.log("Picture uploaded successfully");
      console.log(resultEvent.info.url);
      this.setState({ head: resultEvent.info.url });
    }
  };
  c2 = (error, resultEvent) => {
    if (resultEvent.event === "success") {
      console.log("Picture uploaded successfully");
      console.log(resultEvent.info.url);
      this.setState({ outfit: resultEvent.info.url });
    }
  };
  render() {
    const widget = window.cloudinary.createUploadWidget(
      {
        cloudName: "kevin14",
        uploadPreset: "zfjpjtrr",
        sources: ["local", "url", "dropbox", "facebook", "instagram"],
      },
      (error, result) => {
        this.checkUploadResult(error, result);
      }
    );
    const widget2 = window.cloudinary.createUploadWidget(
      {
        cloudName: "kevin14",
        uploadPreset: "zfjpjtrr",
        sources: ["local", "url", "dropbox", "facebook", "instagram"],
      },
      (error, result) => {
        this.c(error, result);
      }
    );
    const widget3 = window.cloudinary.createUploadWidget(
      {
        cloudName: "kevin14",
        uploadPreset: "zfjpjtrr",
        sources: ["local", "url", "dropbox", "facebook", "instagram"],
      },
      (error, result) => {
        this.c2(error, result);
      }
    );
      const list = this.state.layer
    return (
      <div className="create">
        <NavBar />
        <TopNavBar />
        <div className="create-container">
          <div className="name-container">
            <p>Colletion Name</p>
            <DragDropContext onDragEnd={(param)=>{
              const srcI = param.source.index
              const desI = param.destination?.index;
              if(desI){
                list.splice(desI,0,list.splice(srcI,1)[0]);
               
              } 
            }}>
              <div className="layerOrder-container">
                <p id = 'layer-ttl'>Layer Orders</p>
                <div id = 'subhed'><p>layer name</p> <p>traits</p></div>
                <Droppable droppableId="droppable-1" type="PERSON">
                  {(provided, snapshot) => (
                    <div ref={provided.innerRef} {...provided.droppableProps}>
                      {this.state.layer.map(({ name, id }, index) => {
                        return (
                          <Draggable key={id} draggableId={id} index={index}>
                            {(provided, snapshot) => (
                              <div
                                ref={provided.innerRef}
                                {...provided.draggableProps}
                                {...provided.dragHandleProps}
                              >
                                <div className = 'map-lay'>
                                    <div id ="arrow"></div>
                                    <p>{name}</p>
                                  
                                </div>
                              </div>
                            )}
                          </Draggable>
                        );
                      })}
                     
                      {provided.placeholder}
                    </div>
                  )}
                </Droppable>
              </div>
            </DragDropContext>
          </div>
          <div className="layers">
            <h1>background</h1>
            <div className="back">
              <div className="map">
                {this.props.background.map((back, index) => {
                  return (
                    <div
                      key={index}
                      id={back.png}
                      className="background-container"
                      onClick={() => this.getDiv(back.png)}
                    >
                      <img className="background-img" src={back.png} />
                      <p>{back.name}</p>
                    </div>
                  );
                })}
              </div>
              <button onClick={() => widget.open()}>Upload</button>
            </div>
            <div>
              <h1>Head</h1>
              <div className="head">
                {" "}
                <div className="map">
                  {this.props.head.map((head) => {
                    return (
                      <div
                        id={head.png}
                        className="head-container"
                        onClick={() => this.getHead(head.png)}
                      >
                        <img className="head-img" src={head.png} />
                        <p id="p-">{head.name}</p>
                      </div>
                    );
                  })}
                </div>
                <button onClick={() => widget2.open()}>Upload</button>
              </div>
            </div>
            <div>
              <h1>Outfit</h1>
              <div className="body">
                {" "}
                <div className="map">
                  {this.props.outfit.map((outfit) => {
                    return (
                      <div
                        id={outfit.png}
                        className="outfit-container"
                        onClick={() => this.getOutfit(outfit.png)}
                      >
                        <img className="head-img" src={outfit.png} />
                        <p id="p-">{outfit.name}</p>
                      </div>
                    );
                  })}
                </div>
                {/* <input type="file" onChange={this.showFile} /> */}
                <button onClick={() => widget3.open()}>Upload</button>
              </div>
            </div>
          </div>
          <div className="nft-container">
            <div className="nft">
              <div className='final-img'>
                <img id="nft-png1" src={this.state.background}></img>
                <div className ='head-out'>
                <img id="nft-png2" src={this.state.head}></img>
                <img id="nft-png3" src={this.state.outfit}></img></div>
              </div>
            </div>
            <div className="decription"></div>
            
            <div className="mint-dwnld">
              <h3 className="download-btn">
                <a
                  href={this.state.background}
                  download={this.state.background}
                  href={this.state.head}
                  download={this.state.head}
                  href={this.state.outfit}
                  download={this.state.outfit}
                  style={({ textDecoration: "none" }, { color: "white" })}
                >
                  Download
                </a>
              </h3>
              {/* <button onClick={this.merge}></button> */}
              <h3 className="mint">mint {this.state.mint}</h3>
            </div>
            <div className="mnt-comb">
              <div className="mnt-con">
                <p>mint ammount</p>
                <input type="number" defaultValue="1000" onChange ={this.handleChange}/>
              </div>
              <div className="mnt-con">
                <p>combinations</p></div>
              
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (reduxState) => {
  return {
    background: reduxState.pngReducer.background,
    head: reduxState.pngReducer.head,
    outfit: reduxState.pngReducer.outfit,
  };
};
export default connect(mapStateToProps, {
  getBackground,
  getHead,
  getOutfit,
})(Create);
